import React, { useCallback, useMemo, useState } from "react";
import { makeData } from "./data";
import "./styles.css";
import {
  Tabs,
  Panel,
  DragTabList,
  PanelList,
  helpers,
  Tab,
  TabList,
  ExtraButton,
  AsyncPanel
} from "@react-tabtab-next/tabtab";
import { md, bootstrap, bulma } from "@react-tabtab-next/themes";

export default function App() {
  const [activeTab, setActiveTab] = useState(0);
  const [tabs, setTabs] = useState(makeData(25, "Some Tab"));

  const closableTabItems = useMemo(() => {
    return tabs.map((tab, index) => {
      return (
        <Tab closable key={index}>
          {tab.title}
        </Tab>
      );
    });
  }, [tabs]);

  const tabItems = useMemo(() => {
    return tabs.map((tab, index) => {
      return <Tab key={index}>{tab.title}</Tab>;
    });
  }, [tabs]);

  const shortTabItems = useMemo(() => {
    return makeData(3, "Tab").map((tab, index) => {
      return <Tab key={index}>{tab.title}</Tab>;
    });
  }, []);

  const panelItems = useMemo(() => {
    return tabs.map((tab, index) => {
      return <Panel key={index}>{tab.content}</Panel>;
    });
  }, [tabs]);

  const handleOnTabSequenceChange = useCallback(
    ({ oldIndex, newIndex }: { oldIndex: number; newIndex: number }) => {
      console.log({ oldIndex, newIndex });
      setTabs((tabs) => helpers.simpleSwitch(tabs, oldIndex, newIndex));
      setActiveTab(newIndex);
    },
    []
  );

  const handleOnTabChange = useCallback((i) => {
    console.log("select tab", i);
    setActiveTab(i);
  }, []);

  function loadContentFunc(callback: (err: any, data: any) => void) {
    setTimeout(() => {
      callback(null, "some async content");
    }, 1000);
  }

  return (
    <div className="App">
      <p className="title">Material draggable</p>
      <Tabs
        customStyle={md}
        activeIndex={activeTab}
        onTabChange={handleOnTabChange}
        onTabSequenceChange={handleOnTabSequenceChange}
        ExtraButton={
          <ExtraButton
            onClick={(e) => {
              console.log(e);
            }}
          >
            +
          </ExtraButton>
        }
      >
        <DragTabList>{tabItems}</DragTabList>
        <PanelList>{panelItems}</PanelList>
      </Tabs>

      <p className="title">Bootstrap closable</p>
      <Tabs
        onTabClose={(i) => {
          console.log("close", i);
        }}
        showModalButton={false}
        customStyle={bootstrap}
        activeIndex={activeTab}
        onTabChange={handleOnTabChange}
        onTabSequenceChange={handleOnTabSequenceChange}
      >
        <TabList>{closableTabItems}</TabList>
        <PanelList>{panelItems}</PanelList>
      </Tabs>

      <p className="title">Bulma draggable</p>
      <Tabs
        customStyle={bulma}
        activeIndex={
          activeTab > shortTabItems.length - 1
            ? shortTabItems.length - 1
            : activeTab
        }
        onTabChange={handleOnTabChange}
        onTabSequenceChange={handleOnTabSequenceChange}
      >
        <DragTabList>{shortTabItems}</DragTabList>
        <PanelList>{panelItems}</PanelList>
      </Tabs>
      <p className="title">Async data loading</p>
      <Tabs defaultIndex={0}>
        <TabList>
          <Tab>Tab1</Tab>
          <Tab>Tab2</Tab>
        </TabList>
        <PanelList>
          <Panel>Static content</Panel>
          <AsyncPanel
            loadContent={loadContentFunc}
            render={(data) => <div>{JSON.stringify(data)}</div>}
            renderLoading={() => <div>Loading...</div>}
            cache={false}
          />
        </PanelList>
      </Tabs>
    </div>
  );
}
